import React from "react";
import { Paper, Divider } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles, MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";
import { ProductType } from "../../types/product";
import CardMap from "../../CardMap";

const useStyles = makeStyles(theme => ({
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px"
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px"
    }
}));

type CardStoryProps = {
    brand: BrandType;
    isLoading: boolean;
    imgs: [];
    notAvaliable: boolean;
    meal: string;
    isFavorite: boolean;
    title: string;
    stars: number;
    product: ProductType;
    totalPrice: string;
    oldPartialPrice: string;
    partialPrice: string;
    totalPeople: string;
    totalDays: string;
    mode: string;
};

const CardStory = ({ brand, isLoading, imgs, notAvaliable, meal, isFavorite, title, stars, product, totalPrice, oldPartialPrice, partialPrice, totalPeople, totalDays, mode }: CardStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="checkbox">
                <Typography variant="h3">Controles de seleção</Typography>
                <br />
                <Divider />
                <br />
                <Grid container spacing={5}>
                    <Grid item xs={3}>
                        <CardMap
                            isLoading={isLoading}
                            imgs={imgs}
                            notAvaliable={notAvaliable}
                            meal={meal}
                            isFavorite={isFavorite}
                            title={title}
                            stars={stars}
                            product={product}
                            totalPrice={totalPrice}
                            oldPartialPrice={oldPartialPrice}
                            partialPrice={partialPrice}
                            totalPeople={totalPeople}
                            totalDays={totalDays}
                        />
                    </Grid>
                </Grid>
            </Paper>
        </MuiThemeProvider>
    );
};

storiesOf("Components/Cards/CardMap", module).add("Controls", args => <CardStory {...args} />, {
    args: {
        brand: "CVC",
        product: "hotel",
        notAvaliable: false,
        meal: "lunch",
        title: "Title",
        stars: 3,
        totalPrice: "200",
        oldPartialPrice: "200",
        partialPrice: "100",
        totalPeople: "200",
        totalDays: "2",
        imgs: ["http://lorempixel.com/output/city-q-c-298-285-3.jpg"],
        isLoading: false,
        mode: "root"
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        isLoading: { type: "boolean" },
        imgs: { type: "array" },
        notAvaliable: { type: "boolean" },
        meal: { type: "string" },
        isFavorite: { type: "boolean" },
        title: { type: "string" },
        stars: { type: "number" },
        product: {
            options: ["package", "air", "hotel"],
            control: { type: "select" }
        },
        totalPrice: { type: "string" },
        oldPartialPrice: { type: "string" },
        partialPrice: { type: "string" },
        totalPeople: { type: "string" },
        totalDays: { type: "string" },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        }
    }
});
